@import url(https://fonts.googleapis.com/css?family=Nunito:400,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
@font-face {
  font-family: 'Digital';
  src: url("/bombbeats/static/media/digital-7 (mono).58045dab.ttf");
}

body {
  background-color: #212121;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

button {
  border: 0;
  padding: 0;
  margin: 0;
  outline: none;
}
.App {
  padding-top: 40px;
  display: flex;
  flex-flow: column;
}

.App--beat-select {
  width: 808px;
  margin: auto;
}
.Track-container {
    display: flex;
    /* background-color: #999; */
    height: 40px;
    align-items: center;
}
.TouchPad-container {
    cursor: pointer;
    border-radius: 5px;
    /* background-color: #333; */
    /* LIGHT THEME */
    background-color: #CCC;
    height: 32px;
    width: 32px;
    margin: 0 4px 0 0;
    -webkit-transition: background-color .1s ease;
    transition: background-color .1s ease;
}

.TouchPad-container:nth-child(4n) {
    margin: 0 10px 0 0;
}

.TouchPad-container:last-child {
    margin: 0;
}

.TouchPad-container.active {
    background-color: rgb(76, 170, 253);
}

.TouchPad-container.active:hover {
    background-color: rgb(111, 188, 255);
}

.TouchPad-container.active.disabled {
    background-color: #999;
}

.TouchPad-container:hover {
    /* background-color: #212121; */
    /* LIGHT THEME */
    background-color: #afafb9;
}
 .Controls-container { 
    display: flex;
    align-items: center;
    margin: 0 110px 12px 110px;
 }

 .playback {
    display: flex;
    align-items: center;
    box-sizing: border-box;
    width: 128px;
    height: 64px;
    border: 1px solid #999;
    border-radius: 5px;
    overflow: hidden;
 }

 .playback button{
     cursor: pointer;
     outline: none;
     border: 0;
     padding: 0;
     margin: 0;
     /* background-color: #424242; */
     /* LIGHT THEME */
     background-color: #666;

     height: 64px;
     width: 64px;
     line-height: 64px;
     font-family: 'Material Icons';
     font-size: 48px;
     color: #212121;
     -webkit-transition: background-color .1s ease;
     transition: background-color .1s ease;
 }

 .playback button:hover {
    /* background-color: #333; */
    /* LIGHT THEME */
    background-color: #424242;
 }

 button.play {
    -webkit-transition: color .3s ease;
    transition: color .3s ease;
}
button.play:not(.active):hover  {
    color: rgb(76, 170, 253);
}
button.play.active {
    -webkit-animation: blinking 1.3s infinite;
            animation: blinking 1.3s infinite;
}

button.stop {
    -webkit-transition: color .3s ease;
    transition: color .3s ease;
}

button.stop.active:hover {
    color: rgb(76, 170, 253);
}


.bpm-slider-container {
    flex-grow: 1;
    text-align: right;
    padding-right: 8px;
}

/* The slider itself */
.bpm-slider {
    -webkit-appearance: none;  /* Override default CSS styles */
    -moz-appearance: none;
         appearance: none;
    width: 100px; /* Full-width */
    height: 4px; /* Specified height */
    border-radius: 2px;
    background: #212121; /* Grey background */
    outline: none; /* Remove outline */
  }
  
  /* The slider handle (use -webkit- (Chrome, Opera, Safari, Edge) and -moz- (Firefox) to override default look) */
  .bpm-slider::-webkit-slider-thumb {
    -webkit-appearance: none; /* Override default look */
    appearance: none;
    border-radius: 6px;
    width: 12px; /* Set a specific slider handle width */
    height: 32px; /* Slider handle height */
    background:  rgb(76, 170, 253); /* Green background */
    cursor: pointer; /* Cursor on hover */
  }

    /* Mouse-over effects */
.bpm-slider::-webkit-slider-thumb:hover {
    opacity: 1; /* Fully shown on mouse-over */
}

.bpm-readout {
    width: 108px;
    height: 64px;
    border-radius: 5px;
    border: 1px solid #999;
    background-color: #171717;
    box-sizing: border-box;
    text-align: right;
}
.bpm-readout div {
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-family: 'Digital';
    color: rgb(76, 170, 253);
    line-height: 68px;
    font-size: 48px;
    padding-right: 20px;
}


@-webkit-keyframes blinking {
    0%{
        color: rgb(76, 170, 253);
    }
    50% {
        color: #212121;
    }
    100%{
        color: rgb(76, 170, 253);
    }
}


@keyframes blinking {
    0%{
        color: rgb(76, 170, 253);
    }
    50% {
        color: #212121;
    }
    100%{
        color: rgb(76, 170, 253);
    }
}
.SquareBacklitButton--button {
    position: relative;
    cursor: pointer;
    font-family: monospace;
    word-wrap: break-word;
    text-transform: uppercase;
    padding: 6px;
    width: 48px;
    height: 48px;
    border-radius: 5px;
    background-color: rgb(238, 237, 226);
    margin-left: 8px;
    margin-bottom: 8px;
    -webkit-transition: background-color 0.3s ease-out;
    transition: background-color 0.3s ease-out;
}

.SquareBacklitButton--button.enabled {
    background-color: rgb(255, 248, 151);
}

.SquareBacklitButton--icon {
    position: absolute;
    top: 8px;
    left: 8px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    height: 32px;
}
.Effects-container {
    position: absolute;
    display: flex;
    flex-wrap: wrap;
    top: 0;
    left: 100%;
    width: 60px;
    padding: 4px 0 0 4px;
    box-sizing: border-box;
}

.Effects-container button {
    cursor: pointer;
    font-family: monospace;
    word-wrap: break-word;
    text-transform: uppercase;
    padding: 6px;
    width: 48px;
    height: 48px;
    border-radius: 5px;
    background-color: #eee;
    margin-left: 8px;
    margin-bottom: 8px;
    -webkit-transition: background-color 0.3s ease-out;
    transition: background-color 0.3s ease-out;
}

.Effects-container button.enabled {
    background-color: rgb(255, 248, 151);
}
.RunningLights-container {
    margin: 16px 0 4px 110px;
    display: flex;
}

.RunningLights-light {
    border-radius: 5px;
    background-color: #171717;
    height: 12px;
    width: 32px;
    margin: 0 4px 0 0;
    -webkit-transition: background-color .01s ease-out;
    transition: background-color .01s ease-out;
}

.RunningLights-light.active {
    background-color: rgb(255, 248, 151);
}

.RunningLights-light:nth-child(4n) {
    margin: 0 10px 0 0;
}
.Sequencer {
    position: relative;
    width: 808px;
    height: 100%;
    /* background-color: #616161; */
    /* LIGHT THEME */
    background-color: #f9f9f0;

    margin: 0 auto;
    margin-top: 16px;
    padding: 40px 0;
    border: 1px solid #CCC;
    border-radius: 4px;
}

.Sequencer-sequencer {
    display: flex;
}

.Sequencer-tracklist {
    width: 100px;
    margin-top: 4px;
    margin-right: 10px;
}

.Sequencer-track-label {
    display: flex;
    align-items: center;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    border-radius: 5px;
    overflow: hidden;
    margin: 0 0 8px 10px;
    height: 32px;
    line-height: 32px;
    box-sizing: content-box;
    font-family: 'Nunito', 'sans-serif';
    font-weight: bold;
    text-align:right;
    flex-shrink: 0;
}

.Sequencer-track-name {
    background-color: #ccc;
    color: #212121;
    padding-right: 8px;
    flex-grow: 1;
}

.Sequencer-track-indicator {
    cursor: pointer;
    width: 24px;
    height: 32px;
    flex-shrink: 0;
    background-color: #333;
    -webkit-transition: background-color 0.3s ease;
    transition: background-color 0.3s ease;
}

.Sequencer-track-indicator.active {
    background-color: rgb(255, 248, 151);
}

.Sequencer-trackname:last-child {
    margin-bottom: 0;
}

.Sequencer-track-container {
    flex-grow: 1;
}
.TrackPad--container {
    background-color: #CCC;
    height: 108px;
    width: 592px;
    margin: 0 auto;
    border-radius: 12px;
    -webkit-transition: background-color .65s ease;
    transition: background-color .65s ease;
}

.TrackPad--container.active {
    background-color: rgb(76, 170, 253);
    -webkit-animation: hum 1.3s infinite;
            animation: hum 1.3s infinite;
    -webkit-animation-delay: 0.65s;
            animation-delay: 0.65s;
}

@-webkit-keyframes hum {
    0%{
        background-color: rgb(76, 170, 253);
    }
    50% {
        background-color: rgb(128, 193, 250);
    }
    100%{
        background-color: rgb(76, 170, 253);
    }
}

@keyframes hum {
    0%{
        background-color: rgb(76, 170, 253);
    }
    50% {
        background-color: rgb(128, 193, 250);
    }
    100%{
        background-color: rgb(76, 170, 253);
    }
}
.Synth--container {
    position: relative;
    width: 808px;
    background-color: #f9f9f0;
    margin: 0 auto;
    margin-top: 16px;
    padding: 40px 0;
    border: 1px solid #CCC;
    border-radius: 4px;
}

.Synth--label {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
    position: absolute;
    font-family: Nunito;
    font-size: 12px;
    left: 60px;
    top: 84px;
    color: #333;
}
.WaveformSelector--container {
    display: flex;
    align-items: center;
    background-color: #333;
    margin: 0 auto;
    margin-top: 32px;
    padding: 8px 4px 0 4px;
    border-radius: 6px;
    top: 0;
    left: 100%;
    width: 238px;
    height: 64px;
    box-sizing: border-box;
}

